import { WeElement, define, render } from 'omi'
import '../../src/table'
import '../../src/input-table'

define('my-app', class extends WeElement {
  dataSource = [{
    id: 1,
    name: 'xwang',
    age: 18,
    address: ''
  }, {
    id: 2,
    name: 'dntzhang',
    age: 12,
    address: ''
  }, {
    id: 3,
    name: 'dntzhang2',
    age: 12,
    address: ''
  }, {
    id: 4,
    name: 'dntzhang3',
    age: 12,
    address: ''
  }, {
    id: 5,
    name: 'dntzhang4',
    age: 12,
    address: ''
  }];

  columns = [{
    title: '',
    key: 'name',
  }, {
    title: '',
    key: 'age',
  }, {
    title: '',
    key: 'address',
  }]


  render() {
    return (
      <div>

        <o-input-table
          dataSource={this.dataSource}
          columns={this.columns}>
        </o-input-table>
      </div>


    )
  }
})

render(<my-app />, 'body')



